body {
    font-family: 'Roboto', Arial, Helvetica, sans-serif;
    background-color: $dark-gray;
    //background-color: rgba(42, 42, 46, 1);
    color:white;
    max-width: 1380px;
    //border: yellow 2px solid;
    //height: 820px;
}

h1 {
    display: flex;
    width: 100%;
    max-width: 1300px;
    height: 100px;
    color: white;
    span {
        font-family: 'Limelight', cursive;
        margin-right: auto;
        margin-left: 50%;
        transform: translateX(-50%);
    }
    font-weight: 700;
    font-size: 100px;
    padding-top: 15px;
}

.main-content {
    display: flex;
    //border: red 2px solid;
    //height: 700px;
}

.right-container {
    //margin-top: 100px;
    margin-left: 30px;
}

#canvas {
    position: absolute;
    background-color: $dark-gray;
    //background-color:rgba(42, 42, 46, 1);
}

#board {
    border-radius: 30px;
    border: 5px solid black;
    margin-top: 30px;
}

#spacer {
    height: 90px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.button {
    margin-bottom: 5px;
    height: 40px;
    font-size: inherit;
    border-radius: 5px;
    font-weight: 700;
    background-color: $gray;
    box-shadow: 0 3px $gray-shadow;
    display: flex;
    align-items: center;
    &:active {
        //background-color: $gray-shadow;
        //box-shadow: none;
        //top: 3px;
    }
    &:hover{
        //background-color: $gray-shadow-light;
        //cursor: pointer;
    }
}

h2 {
    font-weight: 700;
    opacity: 70%;
    font-size: 20px;
    //margin-right: auto;
    //margin-left: 50%;
    //transform: translateX(-50%);
}

#pool-heading {
    width:200px;
    display: flex;
    justify-content: center;
}

#piece-pool-container {
    //position: relative;
    width: 250px;
    height: 700px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#left-container {
    //border: green 2px solid;
    //height: 700px;
    //position: fixed;
    //left: 0;
    //top: 100px;
}

.message-container {
    display: flex;
    width: 810px;
    height: 160px;
    margin-top: 2px;
    //margin-top: 90px;
    padding-left: 10px;
    box-sizing: border-box;
    background-color: white;
    opacity: 70%;
    border: 5px solid black;
    border-radius: 30px;
    color:black;
    font-size: 40px;
    white-space: pre-line;
    //margin-right: 0px;
    //margin-left: 50%;
    //transform: translateX(-50%);
    align-items: center;
    justify-content: center;;
    p {
        //width: 100%;
        //margin-left: 50%;
        //transform: translateX(-50%);
    }
}

#piece-pool {
    box-sizing: border-box;
    height: 700px;
    width: 200px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-top: 5px;
}

.piece-pool-item {
    position: relative;
    width: 75px;
    height: 75px;
    margin: 5px;
    z-index: 5;
    &.selected {
        background-color: rgba(128, 128, 128, .7);
    }
    border: 5px solid black;
    border-radius: 15px;
    
}

#pieces-to-play {
    display:flex;
    flex-direction: column;
    align-items: center;
    //margin-right: 150px;
}

#pieces-to-play-container {
    display: flex;
    justify-content: center;
    .piece-to-play-container {
        margin: 10px;
        h2 {
            display: flex;
            justify-content: center;
        }
        canvas {
            //margin-top: 5px;
            border: 5px solid black;
            border-radius: 15px;
            margin-bottom: 5px;
        }
    }
}

#game {
    //margin-left: 150px;
}

#instructions {
    li {
        
        li {
            list-style: disc;
            margin-left: 15px;
            margin-top: 5px;
        }
    }
}

#footer {
    margin-top: 20px;
    font-size: 25px;
    width: 100%;
    max-width: 1300px;
    display: flex;
    justify-content: center;
    color: $gray;

    .developer-info {
        //height: 24%;
        width: 100%;
        display: flex;
        justify-content: center;
        span {
            padding-right: 25px;
        }
    }
    .icons-container {
        display: flex;
        justify-content: space-around;
    }
    .fab, .far {
        height: 25px;
        padding: 0 5px;
    }
    .fab:hover, .far:hover {
        color: black;
        background: $gray;
    }
}
